.movieItemTitle {
  height: 40px;
  text-align: center;
  font-size: 20px;
  color: rgb(255, 69, 0);
}
.movieItemItem {
  display: flex;
  justify-content: center;
  background: rgb(23, 33, 43);
  border-radius: 15px;
  box-sizing: border-box;
  overflow: hidden;
  margin-left: 30px;
  margin-top: 30px;
  margin-bottom: 30px;
  border-radius: 15px;
}

.movieItemImg {
  width: 100%;
}
